<template>
  <div class="mainXiaoGuo">
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
      </div>
      <h1>My Orders</h1>
      <div class="chartWrap">
        <router-link :to="{ path: '/search', query: {} }">
          <span class="search iconfont"> &#xe625; </span>
        </router-link>
        <span class="bag" v-if="indexData.shoppingCartNum > 0">
          <em class="font10"> {{ indexData.shoppingCartNum }} </em>
        </span>
        <a
          href="javascript:void(0)"
          class="iconfont cart"
          @click="$router.push('cart')"
        >
          &#xe669;
        </a>
      </div>
    </section>
    <!-- @click="$router.push({path:'/orderDetail',query:{orderNo:item.orderNo})" -->
    <section class="tabNav">
      <div
        v-for="(item, i) in navList"
        :key="i"
        :class="navIndex == i ? 'active' : ''"
        @click="change(i)"
      >
        <span> {{ item.name }}</span>
      </div>
      <!-- <div class="active"> <span> All </span> </div>
        <div> <span> Unpaid </span> </div>
        <div> <span> Transporting </span> </div>
        <div> <span> Done </span> </div> -->
    </section>

    <section class="orderList" v-if="!isEmpty">
      <van-pull-refresh
        v-model="isLoading"
        @refresh="onRefresh"
        loosing-text="Release to refresh"
        loading-text="Loading..."
      >
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="The end"
          @load="onLoad"
        >
          <ul>
            <li v-for="(item, index) in orderList" :key="index">
              <div
                class="topNo"
                @click="
                  $router.push({
                    path: '/orderDetail',
                    query: { orderNo: item.orderNo }
                  })
                "
              >
                <p>
                  <span class="no"> {{ item.orderNo }} </span>
                  <span class="payStyle"> {{ item.payType }} </span>
                </p>
                <p>
                  <span class="payStatus"> {{ item.orderStatusStr }} </span>
                </p>
              </div>
              <div
                class="proWrap clearfix"
                v-for="(item_child, index_child) in item.prodList"
                :key="index_child"
                @click="
                  $router.push({
                    path: '/orderDetail',
                    query: { orderNo: item.orderNo }
                  })
                "
              >
                <div
                  class="imgWrap"
                  @click="
                    $router.push({
                      path: '/orderDetail',
                      query: { orderNo: item.orderNo }
                    })
                  "
                >
                  <img :src="item_child.img0" class="img" />
                </div>
                <div class="numRight">
                  <span class="cheng"> X </span>
                  <span> {{ item_child.productQuantity }} </span>
                </div>
                <div class="rightMain">
                  <p class="name">{{ item_child.productName }}</p>
                  <p class="size">
                    {{ getAttributes(item_child.attrOptList) }}
                  </p>
                </div>
              </div>
              <div
                class="downRow"
                @click="
                  $router.push({
                    path: '/orderDetail',
                    query: { orderNo: item.orderNo }
                  })
                "
              >
                <p class="date">{{ StringToDate(item.createDate) }}</p>
                <p>
                  <span class="color999"> Grand Total: </span>
                  <span class="total">
                    {{ (item.payedAmount / 100).toFixed(2) }} AED
                  </span>
                </p>
              </div>
              <div class="downBtns" v-if="item.orderStatus == 101">
                <van-button plain hairline type="primary" @click="cancel(item)"
                  >Cancel</van-button
                >
                <van-button
                  plain
                  hairline
                  type="danger"
                  @click="
                    $router.push({
                      path: '/payMent',
                      query: { orderNo: item.orderNo }
                    })
                  "
                  >Pay</van-button
                >
              </div>
              <div
                class="downBtns"
                v-if="item.orderStatus == 103 || item.orderStatus == 102"
              >
                <span class="tips" v-if="item.logisticsInformationList">
                  {{ item.logisticsInformationList[0].remark }}</span
                >
                <van-button
                  plain
                  hairline
                  type="primary"
                  @click="received(item.orderNo)"
                  >Received</van-button
                >
                <van-button
                  plain
                  hairline
                  type="primary"
                  @click="
                    $router.push({
                      path: '/logistics',
                      query: { orderNo: item.orderNo }
                    })
                  "
                  >Logistic</van-button
                >
              </div>
              <div class="downBtns" v-if="item.orderStatus == 104">
                <van-button
                  plain
                  hairline
                  type="primary"
                  @click="
                    this.$router.push({
                      path: 'reviewsOrder',
                      query: { orderNo: item.orderNo }
                    })
                  "
                  >Review</van-button
                >
                <van-button plain hairline type="danger" @click="addCart(item)"
                  >Buy Again</van-button
                >
              </div>
              <div class="downBtns" v-if="item.orderStatus == 202">
                <van-button plain hairline type="danger" @click="addCart(item)"
                  >Buy Again</van-button
                >
              </div>
            </li>
          </ul>
        </van-list>
      </van-pull-refresh>
    </section>

    <template v-if="isEmpty">
      <div class="emptycenter">
        <img src="../common/images/myOrdersEmpty.png" />
      </div>
      <span class="tips">You don't have any orders</span>
      <div class="emptyDiv">
        <van-button type="danger" @click="$router.push('flashSale')"
          >EXPLORE OUR DEALS</van-button
        >
      </div>
    </template>
  </div>
</template>

<script>
import {
  getOrderTypes,
  getAllOrder,
  cancelOrder,
  confirmOrder
} from "@/api/order";
import { mapGetters } from "vuex";
export default {
  name: "orderList",
  data() {
    return {
      navIndex: 0,
      navList: [],
      orderList: [],
      query: {},
      loading: false,
      finished: false,
      isLoading: true,
      isEmpty: false,
      orderStatus: ""
    };
  },
  computed: {
    ...mapGetters(["indexData"])
  },
  mounted() {
    this.bus.$emit("showLoading");
    this.init();
  },
  methods: {
    init() {
      this.bus.$emit("loadIndexData");
      this.query = Object.assign(this.query, { pageNo: 1, pageSize: 10 });
      getOrderTypes().then(res => {
        this.navList = res.data.result;
        this.orderStatus = res.data.result[0].orderStatus;
        getAllOrder(this.orderStatus, this.query.pageNo, this.query.pageSize)
          .then(res => {
            this.orderList = res.data.result.result;
            this.isLoading = false;
            if (res.data.result.totalNum <= this.orderList.length) {
              this.finished = true;
            }
            if (res.data.result.result.length == 0) {
              this.isEmpty = true;
            }
          })
          .catch(e => {
            this.isEmpty = true;
            this.orderList = [];
            this.finished = true;
            this.isLoading = false;
          });
      });
      this.bus.$emit("hideLoading");
    },
    goBack() {
      this.$router.back(-1);
    },
    // 切换
    change(i) {
      this.navIndex = i;
      this.orderStatus = this.navList[i].orderStatus;
      this.onRefresh();
    },
    onLoad() {
      if (this.isLoading) {
        this.loading = false;
        return false;
      }
      this.query.pageNo++;
      let query = this.query;
      getAllOrder(this.orderStatus, this.query.pageNo, this.query.pageSize)
        .then(res => {
          this.orderList = this.orderList.concat(res.data.result.result);
          this.loading = false;
          if (res.data.result.totalNum <= this.orderList.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.loading = false;
        });
    },
    onRefresh() {
      this.query = Object.assign(this.query, { pageNo: 1, pageSize: 10 });
      getAllOrder(this.orderStatus, this.query.pageNo, this.query.pageSize)
        .then(res => {
          this.orderList = res.data.result.result;
          this.isLoading = false;
          if (res.data.result.totalNum <= this.orderList.length) {
            this.finished = true;
          }
          if (res.data.result.result.length == 0) {
            this.isEmpty = true;
          } else {
            this.isEmpty = false;
          }
        })
        .catch(e => {
          this.isEmpty = true;
          this.orderList = [];
          this.finished = true;
          this.isLoading = false;
        });
    },
    //添加购物车
    addCart(item) {
      item.prodList.forEach(e => {
        this.$store
          .dispatch("addCart", { skuCode: e.skuCode, num: e.productQuantity })
          .then(res => {});
      });

      this.$toast("Items have been added to Cart .");
    },
    getAttributes(arr) {
      return arr.map(t => t.optionName).join(";");
    },
    cancel(item) {
      this.$dialog
        .confirm({
          message: "Are you sure to cancel this order?",
          confirmButtonText: "confirm",
          cancelButtonText: "Cancel"
        })
        .then(() => {
          // on confirm
          cancelOrder({ orderNo: item.orderNo }).then(res => {
            this.init();
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    received(orderNo) {
      confirmOrder({
        orderNo: orderNo
      }).then(res => {
        this.$toast("Received Successfully");
        this.init();
      });
    },
    StringToDate(DateStr) {
      var converted = Date.parse(DateStr);
      var myDate = new Date(converted);
      if (isNaN(myDate)) {
        //var delimCahar = DateStr.indexOf('/')!=-1?'/':'-';
        var arys = DateStr.split("-");
        myDate = new Date(arys[0], --arys[1], arys[2]);
      }
      var now = myDate;
      var year = now.getFullYear(); //年
      var month = now.getMonth() + 1; //月
      var day = now.getDate(); //日
      var hh = now.getHours(); //时
      var mm = now.getMinutes(); //分
      var ss = now.getSeconds(); //秒
      if (day < 10) clock += "0";
      var clock = day + "/";

      if (month < 10) clock += "0";
      clock += month + "/";

      clock += year + " ";

      return clock;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/orderList.scss";

.emptycenter {
  width: 100%;
  padding: 0 4%;
  display: flex;
  justify-content: center;
  img {
    width: 245px;
    height: 109px;
    margin-top: 100px;
  }
}
.tips {
  display: block;
  width: 100%;
  font-size: 18px;
  color: #c3c3c3;
  text-align: center;
  padding-top: 40px;
}
.emptyDiv {
  .van-button--danger {
    width: 92%;
    margin-left: 4%;
    margin-top: 135px;
  }
}
</style>



